const recorderManager = uni.getRecorderManager();
const innerAudioContext = uni.createInnerAudioContext();

innerAudioContext.autoplay = true;

export default {
	data() {
		return {
			region:'选择位置',
			editorCtx:'',//文本域上下文
			contactArr:[
				{
					name:'班主任',phone:'18621582789',
				},{
					name:'学校保卫室',phone:'18621582789',
				},{
					name:'110',phone:'18621582789',
				},
			],
			recordStatus:'1',// 录音状态 1 就绪 2 正在录制 3 已有一份音频文件
			audioStatus:'播放文件',
			text: 'uni-app',
			voicePath: '',
			form:{
				field06:'0',
				field07:'无',
				field09:''
			},
			list1: [
				{
					value: '0',
					name: '健康',
					checked:true,
				},
				{
					value: '1',
					name: '有发烧、咳嗽等症状'
				},
				{
					value: '2',
					name: '其他症状'
				}
			],
			list2:[
				{
					value:'无',
					name:'无'
				},{
					value:'有',
					name:'有'
				}
			]
		};
	},
	computed:{
		audioText(){
			let audioObj = {
				1:'录制',
				2:'结束录制',
				3:'重新录制'
			}
			return audioObj[this.recordStatus]
		}
	},
	mounted() {
		this.judgeAuthorize()
		innerAudioContext.onEnded(()=>{
			this.audioStatus = '播放文件'
		})
	},
	methods: {
		handleCancelChoose(){
			this.$refs.linkage.hide()
		},
		showChoose(){
			this.$refs.linkage.show()
		},
		handleChoose({checkArr,checkValue,defaultVal,result}){
			this.$refs.linkage.hide()
			this.region = result
			this.form.field02 = result
		},
		onEditorReady() {
		    uni.createSelectorQuery().select('#editor').context((res) => {
		        this.editorCtx = res.context
		    }).exec()
		},
		handleCall(phoneNumber){
			uni.makePhoneCall({
			    phoneNumber: phoneNumber
			});
		},
		handleRadioChange(evt,name){
			this.form[name] = evt.target.value
		},
		validate(){
			this.editorCtx.getContents({
				success:(data)=>{
					this.form.field09 = data.text
				},
				fail:()=>{
					uni.showToast({
						title:'求助具体要求出现问题!',
						icon:'none'
					})
				},
				complete:()=>{
					// 如果求助要求不是必须项，则使用此方法，否则只调用success方法
					this.sendData()
				}
			})
		},
		judgeAuthorize() {
			uni.getSetting({
				success: info => {
					/*
						{"errMsg":"getSetting:ok","authSetting":{"scope.userInfo":true,"scope.record":true}}
					*/
					if(!info.authSetting['scope.record']){
						uni.authorize({
							scope:'scope.record',
							success:()=>{
								this.initPage()
							},
							fail:()=>{
								uni.showToast({
									title:'请确认录音功能正常',
									icon:'none'
								})
								setTimeout(()=> {
									uni.openSetting({
										success: res => {
											this.initPage()
										}
									});
								}, 1500);
							}
						})
					}else{
						this.initPage()
					}
				}
			});
		},
		initPage(){
			recorderManager.onStop((res) =>{
				this.voicePath = res.tempFilePath;
			});
		},
		handleRecord(){
			if(this.recordStatus == 1 || this.recordStatus == 3){
				recorderManager.start();
				this.recordStatus = 2
			}else if(this.recordStatus == 2){
				recorderManager.stop();
				this.recordStatus = 3
			}
		},
		playVoice() {
			if(this.audioStatus == '播放中...') return;
			if (this.voicePath) {
				innerAudioContext.src = this.voicePath;
				innerAudioContext.play();
				this.audioStatus = '播放中...'
			}
		},
		sendData(){
			// 缺少key值  field01 用户id
			// field08 语音信息
			let params = {
				...this.form,
				field01:uni.getStorageSync('userId')
			}
			this.$HTTP({
				url:'/getHelp/save',
				params,
				successCallback: ({data}) => {
					if(data.code == 0){
						uni.showToast({
							title:'提交成功!'
						})
						setTimeout(()=>{
							uni.navigateBack()
						},1500)
					}else{
						uni.showToast({
							title:data.msg,
							icon:'none'
						})
					}
				}
			})
			// 上传数据
			// uni.uploadFile({
			// 	url:'',//服务器地址
			// 	filePath:'',//文件地址
			// 	name:'',//服务器中文件对应的key值
			// 	formData:{
			// 	},//上传的额外参数
			// 	success:(res)=>{
			// 		console.log(res,'上传成功')
			// 	}
			// })
		}
	}
};
